import React, { Component } from "react";
import { Card, Collapse } from "antd";

/**
 * Error Boundary
 *
 * This is a React component that wraps rendered components inside
 * a boundary so that any errors that the rendered component throws will be
 * caught and thus prevents the entire application from crashing.
 */

export default class ErrorBoundary extends Component {
	state = { error: null, errorInfo: null };

	componentDidCatch(error, errorInfo) {
		this.setState({
			error,
			errorInfo
		});
	}

	render() {
		const { error = {}, errorInfo = {} } = this.state;
		if(error) {
			alert(error.message);
			return (
				<Card>
					<div>{ error.message }</div>
					{
						errorInfo && (
							<Collapse bordered={false}>
								<Collapse.Panel header="Details" key="1">
									{ JSON.stringify(errorInfo.componentStack, null, "\t") }
								</Collapse.Panel>
							</Collapse>
						)
					}
				</Card>
			);
		}

		return this.props.children;
	}
}
