import React from "react";
import ReactDOM from "react-dom";

import "antd/dist/antd.css";
import "./index.css";
import { Root } from "src/views";

import {
  ApolloClient,
	ApolloLink,
  InMemoryCache,
  ApolloProvider,
	HttpLink,
	defaultDataIdFromObject
} from "@apollo/client";
import { setContext } from "@apollo/client/link/context";
import { onError } from "@apollo/client/link/error";


const authLink = setContext(async (_, { headers }) => {
	try {
		// let token = await localStorage.getItem("token");
		let token = "eyJ0eXAiOiJKV1QiLCJhbGciOiJIUzI1NiJ9.eyJpc3MiOiJodHRwczovL2pvYnotc2VydmVyLm9ubGluZSIsImlhdCI6MTYyMjk0MzQ5MywiZXhwIjoxNjU0NDc5NDkzLCJhdWQiOiJqb2J6LXNlcnZlci5vbmxpbmUiLCJzdWIiOiJEZW1vQGpvYnotc2VydmVyLm9ubGluZSIsImdpdmVuTmFtZSI6IkRlbW8iLCJmYW1pbHlOYW1lIjoiSm9iIFoiLCJlbWFpbCI6IkRlbW9Aam9iei1zZXJ2ZXIub25saW5lIn0.C3GPqwDFuT-KBMo5X0yEFabmFa4rP8tppo94j2K8E98";
		return {
			headers: {
				...headers,
				authorization: token ? `Bearer ${token}` : "",
			}
		}
	} catch(error) {
		console.error(error);
	}
});

const errorLink = onError(({ graphQLErrors, networkError, forward, operation }) => {
  if (graphQLErrors) console.error(graphQLErrors);
  if (networkError) console.warn(networkError);
  forward(operation);
});

const httpLink = new HttpLink({
	uri: "https://jobz-server.online/graphql"
});

const client = new ApolloClient({
  uri: httpLink,
	link: ApolloLink.from([errorLink, authLink, httpLink]),
  cache: new InMemoryCache({
		dataIdFromObject: (object) => {
			// return object.id;
			if (object.id) {
					// eslint-disable-next-line no-underscore-dangle
					return `${object.__typename}-${object.id}`;
			}
			if(object.email) {
				return `${object.__typename}-${object.email}`;
			}
			if (object.cursor) {
					// Cursor edge instead, fixes invalid duplicate
					// eslint-disable-next-line no-underscore-dangle
					return `${object.__typename}-${object.cursor}`;
			}
			// Use a fallback to default handling if neither id nor cursor
			return defaultDataIdFromObject(object);
		}
	})
});

ReactDOM.render(
  <ApolloProvider client={client}>
		<Root />
  </ApolloProvider>,
  document.getElementById("root")
);

