import React, { Component } from "react";
import { Button } from "antd";

export default class Chip extends Component {
	render() {
		const { icon, onClick, children } = this.props;
		return (
			<Button
				onClick={() => onClick && onClick()} 
				type="default" 
				shape="round"
				size="small"
				icon={icon}
			>
				{ children }
			</Button>
		);
	}
}
