/* eslint-disable import/no-anonymous-default-export */

import Component from "./component";
import { ErrorBoundary } from "src/components";
import { gql, useQuery, useMutation } from "@apollo/client";

const mutationSearchProfiles = gql`
mutation
GetProfiles($tags: [String], $filter: InputFilter) {
	getProfiles(tags: $tags, filter: $filter) {
		id,
		name,
		description,
		portfolio,
		workExperiences { name, description, years },
		educationExperiences { name, description, years },
		availability { days, dayShift, nightShift },
		tags
	}
}
`;

export default props => {
	const [ getProfiles, loading ] = useMutation(mutationSearchProfiles, { awaitRefetchQueries: true });
	return <ErrorBoundary><Component {...props} loading={loading} getProfiles={getProfiles} /></ErrorBoundary>
}