import React, { Component } from "react";
import { Card, Input, Button, Typography, Row, Col, Collapse, Checkbox, Divider } from "antd";
import { SearchOutlined, CloseCircleFilled, LoadingOutlined, CaretRightOutlined, CaretUpOutlined } from '@ant-design/icons';

import _ from "lodash";
import { Collapse as ProfileCollapse, Chip } from "src/components";
const ALL_DAYS = ["Sun", "Mon", "Tue", "Wed", "Thu", "Fri", "Sat"];


export default class App extends Component {
	state = {
		search: "",
		tags: [],
		activeProfiles: []
	};

	render() {
		const { loading, search, tags } = this.state;
		const isNarrow = this.getWindowDimensions().width <= 576;
		return (
			<>
				{/* <Row gutter={[20, 20]} style={{ display:"flex", flex: 1, alignItems: "center", justifyContent: "flex-start", marginBottom: 40 }}> */}
				<Row gutter={[20, 20]} align="middle" justify={isNarrow ? "center" : "start"}>
					<Col sm={7} style={{ whiteSpace: "pre-wrap" }}>
						<img style={{ width: 150, height: 150, borderRadius: 20 }} src="https://lh3.googleusercontent.com/pw/ACtC-3dYqzXbdX5S7B3EuxIHUgBJKgD37ZhxrXQ-Jo5U2Kio3sRNF9vhR_tVxURXlPZO26ItIsiBxGrfZWV9Wwgooz9z-lNOTUmG2kIg91mDkpEoLSXU3ikFLn3XE3UmTxbdQOayn016bVZb5IlUsr3ZzTuNJQ=s512-no?authuser=0" />
					</Col>
					<Col sm={14}>
						<Typography.Title level={1} style={{ color: "white", marginBottom: 0 }}>Job Z</Typography.Title>
						<Typography.Title level={4} style={{ color: "white", marginTop: 0, marginBottom: 0 }}>Be Discovered</Typography.Title>
					</Col>
				</Row>

				<Row>
					<Col style={{ display: "flex", flexDirection: "row", overflowX: "auto", borderRadius: _.size(tags) > 0 ? 20 : 0, marginTop: 40 }}>
						{ this.renderSearchTags() }
					</Col>
				</Row>

				<Row>
					<Input
						size="large"
						bordered={false}
						style={{ backgroundColor: "white", marginTop: 10, borderRadius: 20 }}
						value={search}
						loading={loading}
						suffix={loading ? <LoadingOutlined /> : <SearchOutlined />}
						placeholder={["Waitress, Bartender, DJ", "App Developer, QA, Beta Tester", "Photographer, Makeup Artist, Influencer"][Math.floor(Math.random() * 3)]}
						onChange={({ target: { value: text }}) => this.setState({ search: text }, () => text.slice(-1) === "," && this.addTag(text.slice(0, -1)))}
						onPressEnter={({ target: { value: text }}) => this.setState({ search: "", loading: true }, () => this.addTag(text))}
					/>
				</Row>

				<Row>
					<Col style={{ borderTopLeftRadius: 20, borderTopRightRadius: 20, marginTop: 10 }}>
						{ this.renderProfiles() }

						<div style={{ marginBottom: 50, whiteSpace: "pre-wrap" }}>
							<Typography.Text style={{ color: "white" }}>
								There are currently 3 profiles tagged with <Chip onClick={() => this.setState({ loading: true }, () => this.addTag("Photographer"))}>Photographer</Chip> <Chip onClick={() => this.setState({ loading: true }, () => this.addTag("Developer"))}>Developer</Chip> <Chip onClick={() => this.setState({ loading: true }, () => this.addTag("Doge"))}>Doge</Chip>
							</Typography.Text>
						</div>
					</Col>
				</Row>
			</>
		);
	}

	renderSearchTags() {
		const { tags, profiles, filter } = this.state;
		return _.size(tags) > 0 ? _.map(tags, tag => (
			<Button
				key={tag}
				onClick={() => this.removeTag(tag)}
				type="default"
				shape="round"
				icon={<CloseCircleFilled />}
				style={{ marginRight: 5 }}
			>
				{ tag }
			</Button>
		)) : (
			<div>
				<Button style={{ width: 0, paddingLeft: 0, paddingRight: 0, border: "none" /* This prevents the search box from nudging upwards */ }} />
				<Typography.Text style={{ color: "white" }}>
					Search profiles by tags separated by comma or <Typography.Text code style={{ color: "white" }}>Enter</Typography.Text> key.
				</Typography.Text>
			</div>
		)
	}

	async addTag(text) {
		const { tags } = this.state;

		if(_.isEmpty(text)) return;
		text = _.toLower(_.trim(text));

		// Cancel previous search call if it's still pending after adding a tag
		const callback = () => {
			clearTimeout(this.timeout);
			this.timeout = _.size(tagList) > 0 && setTimeout(() => this.search(), 500);
		};

		const tagList = _(tags).filter(a => a !== text).concat(text).value();
		this.setState({ tags: tagList, search: "" }, () => callback());
	}

	async removeTag(toRemove) {
		const { tags, profiles } = this.state;
		const tagList = _.filter(tags, a => a !== toRemove);

		// Cancel previous search call if it's still pending after removing another tag
		const callback = () => {
			clearTimeout(this.timeout);
			this.timeout = _.size(tagList) > 0 && setTimeout(() => this.search(), 500);
		};

		this.setState({ tags: tagList, profiles: tagList.length > 0 ? profiles : [] }, () => callback());
	}

	renderProfiles() {
		const { tags, profiles, activeProfiles, loading } = this.state;
		const deactivate = profile => this.setState({ activeProfiles: _.filter(activeProfiles, a => a !== profile.id)});
		const activate = profile => this.setState({ activeProfiles: [...activeProfiles, profile.id]});

		if(_.isEmpty(tags)) {
			return null;
		} else if(_.isEmpty(profiles) && !loading) {
			return (
				<div style={{ marginTop: 20 }}>
					<h2 style={{ color: "white" }}>{ _.size(tags) > 0 && !loading ? "Nothing Here" : "" }</h2>
					<p style={{ color: "white" }}>{ _.size(tags) > 0 && !loading ? "No profiles with specified tags" : "" }</p>
				</div>
			);
		}

		return _.map(profiles, (profile, index) => {
			const isActive = _.indexOf(activeProfiles, profile.id) > -1;
			return (
				<Card
					key={profile.id}
					bordered={false}
					style={{ opacity: loading ? 0.33 : 1, whiteSpace: "pre-wrap", borderRadius: 20, marginBottom: index < _.size(profiles) - 1 ? 10 : 50, overflow: "hidden" }}
					headStyle={{ whiteSpace: "pre-wrap" }}
					bodyStyle={{ padding: 0 }}
					actions={isActive ? [<CaretUpOutlined onClick={() => isActive ? deactivate(profile) : activate(profile)} />] : []}
				>
					{
						_.size(profile.portfolio) > 0 ? (
							<div
								onClick={() => isActive ? deactivate(profile) : activate(profile)}
								style={{ position: "relative", display: "flex", overflowY: "auto", cursor: "pointer" }}
							>
								<img
									src={`https://firebasestorage.googleapis.com/v0/b/job-z-e3be0.appspot.com/o/${profile.portfolio[0]}?alt=media`}
									style={{ width: "100%", height: 250, objectFit: "cover" }}
								/>
								<div
									style={{
										position: "absolute",
										bottom: 0,
										height: 250,
										width: "100%",
										padding: 20,
										display: "flex",
										justifyContent: "space-between",
										flexDirection: "column",
										overflowY: "auto",
										background: "linear-gradient(rgba(0, 0, 0, 0), rgba(0, 0, 0, 0.5))"
									}}
								>
									<div></div>
									<div>
										<Typography.Title level={4} style={{ color: "white" }}>
											{ profile.description }
										</Typography.Title>

										<Typography.Text style={{ color: "white" }}>
										{ _.map(profile.workExperiences, (experience, index) => (<span>{_.isFinite(experience.years) ? `${experience.years} years at ` : ""}<b>{experience.name}</b>{"\n"}</span>)) }
										</Typography.Text>

										<Typography.Text style={{ color: "white" }}>
										{ _.map(profile.educationExperiences, (experience, index) => (<span>{_.isFinite(experience.years) ? `${experience.years} years at ` : ""}<b>{experience.name}</b>{"\n"}</span>)) }
										</Typography.Text>
									</div>
								</div>
							</div>
						) : (
							<div onClick={() => isActive ? deactivate(profile) : activate(profile)} style={{ margin: 20 }}>
								<Typography.Title level={4}>
									{ profile.description }
								</Typography.Title>

								<Typography.Text>
								{ _.map(profile.workExperiences, (experience, index) => (<span>{_.isFinite(experience.years) ? `${experience.years} years at ` : ""}<b>{experience.name}</b>{"\n"}</span>)) }
								</Typography.Text>

								<Typography.Text>
								{ _.map(profile.educationExperiences, (experience, index) => (<span>{_.isFinite(experience.years) ? `${experience.years} years at ` : ""}<b>{experience.name}</b>{"\n"}</span>)) }
								</Typography.Text>
							</div>
						)
					}
						<ProfileCollapse
							height={0}
							active={isActive}
							activate={() => this.setState({ activeProfiles: [...activeProfiles, profile.id]})}
							deactivate={() => this.setState({ activeProfiles: _.filter(activeProfiles, a => a !== profile.id)})}
						>
							{
								_.size(profile.workExperiences) > 0 ? (
									<div style={{ margin: 20 }}>
										<Typography.Title level={5}>Work Experience</Typography.Title>
										<Collapse
											expandIcon={({ isActive }) => <CaretRightOutlined rotate={isActive ? 90 : 0} />}
											ghost
										>
											{
												_.map(profile.workExperiences, (experience, index) => (
													<Collapse.Panel header={`${experience.years} years at ${experience.name}`} key={index}>
														<p>{ experience.description || "-" }</p>
													</Collapse.Panel>
												))
											}
										</Collapse>
									</div>
								) : null
							}

							{
								_.size(profile.educationExperiences) > 0 ? (
									<div style={{ margin: 20 }}>
										<Typography.Title level={5}>Education</Typography.Title>
										<Collapse
											expandIcon={({ isActive }) => <CaretRightOutlined rotate={isActive ? 90 : 0} />}
											ghost
										>
											{
												_.map(profile.educationExperiences, (experience, index) => (
													<Collapse.Panel header={`${experience.years} years at ${experience.name}`} key={index}>
														<p>{ experience.description || "-" }</p>
													</Collapse.Panel>
												))
											}
										</Collapse>
									</div>
								) : null
							}

							{
								_.size(profile.portfolio) > 0 ? (
									<div style={{ margin: 20 }}>
										<Typography.Title level={5}>Portfolio</Typography.Title>
										{ this.renderPortfolio(profile) }
									</div>
								) : null
							}

							{
								_.concat(profile.workExperiences, profile.educationExperiences).length > 0 ?  (
									<Divider style={{ marginTop: 10, marginBottom: 10 }} />
								) : null
							}

							<div style={{ margin: 20, marginTop: 0 }}><Typography.Title level={5}>Availability</Typography.Title></div>
							{ this.renderAvailability(profile) }

							<div style={{ margin: 20 }}>
								<Typography.Title level={5}>Tags</Typography.Title>
								{ this.renderTags(profile.tags) }
							</div>
					</ProfileCollapse>
				</Card>
			);
		});
	}

	search() {
		const { getProfiles } = this.props;
		const { tags, filter } = this.state;

		this.setState({ loading: true }, async () => {
			const profiles = (await getProfiles({ variables: { tags, filter } })).data.getProfiles;
			this.setState({ profiles, loading: false });
		})
	}

	renderPortfolio(profile) {
		const { portfolio } = profile;
		return (
			<div style={{ display: "flex", flexDirection: "row", overflowX: "auto", borderRadius: 20 }}>
				{
					_.map(portfolio, (item, index) => (
						<img
							src={`https://firebasestorage.googleapis.com/v0/b/job-z-e3be0.appspot.com/o/${item}?alt=media`}
							style={{ width: 200, height: 200, borderRadius: 5, marginRight: index < _.size(portfolio) - 1 ? 5 : 0 }}
						/>
					))
				}
			</div>
		);
	}

	renderAvailability(profile) {
		const { availability } = profile;
		const { days, dayShift, nightShift } = availability;
		return (
			<>
				<Row>
					<Col style={{ display: "flex", flex: 1, flexDirection: "row", overflowX: "auto", justifyContent: "space-between", paddingLeft: 20, paddingRight: 20 }}>
					{
						_.map(ALL_DAYS, (day, index) => (
							<Button
								type={_.indexOf(days, day) > -1 ? "primary" : "default"}
								shape="round"
								style={{ display: "flex", flex: 1, borderRadius: 50, justifyContent: "center", paddingLeft: 10, paddingRight: 10, marginRight: index < _.size(ALL_DAYS) - 1 ? 5 : 0 }}
							>
								{ day }
							</Button>
						))
					}
					</Col>
				</Row>

				<div style={{ display: "flex", flexDirection: "row", justifyContent: "center", marginTop: 20 }}>
					<Checkbox checked={dayShift}>Daytime</Checkbox>
					<Checkbox checked={nightShift}>Evening</Checkbox>
				</div>
			</>
		);
	}

	renderTags(tags) {
		if(_.size(tags) > 0) {
			return (
				<div style={{ borderRadius: 20 }}>
				{
					_.map(tags, tag => (
						<Button
							key={tag}
							onClick={() => this.addTag(tag)}
							type="default"
							shape="round"
							style={{ marginRight: 5, marginTop: 5 }}
						>
							{ tag }
						</Button>
					)
					)
				}
				</div>
			);
		} else {
			return (
				<div style={{ opacity: 0 }}><Button /></div>
			);
		}
	}

	getWindowDimensions() {
		const { innerWidth: width, innerHeight: height } = window;
		return { width, height };
	}
}
